<template>
  <div class="container">
    <b-card title="Daftar">
      <b-form class="container" @submit="on_submit">
        <b-form-group class="container my-2" id="input1">
          <label for="text-user">Username</label>
          <b-form-input type="text" id="text-user" v-model="register_form.user" required></b-form-input>
        </b-form-group>
        <b-form-group class="container my-2" id="input4">
          <label for="radio-job">Peran</label>
          <b-form-radio-group
            id="radio-job"
            v-model="register_form.job"
            :options="job_options"
            name="radio-options"
            required
          ></b-form-radio-group>
        </b-form-group>   
        <b-form-group class="container my-2" id="input2">
          <label for="text-password">Password</label>
          <b-form-input 
            type="password" 
            id="text-password" 
            v-model="register_form.password" 
            :state="pass_check(register_form.password, register_form.password2)"
            aria-describedby="password-help-block"
            required
          ></b-form-input>
          <b-form-invalid-feedback id="password-help-block">
            Password tidak sama
          </b-form-invalid-feedback>
        </b-form-group> 
        <b-form-group class="container my-2" id="input3">
          <label for="text-password2">Konfirmasi Password</label>
          <b-form-input 
            type="password" 
            id="text-password2" 
            v-model="register_form.password2" 
            :state="pass_check(register_form.password, register_form.password2)"
            aria-describedby="password-help-block2"
            required
          ></b-form-input>
          <b-form-invalid-feedback id="password-help-block2">
            Password tidak sama
          </b-form-invalid-feedback>
          <!-- <b-form-text id="password-help-block2">Ulangi Password</b-form-text> -->
        </b-form-group> 
        <b-button class="m-3 " type="submit" variant="primary" :disabled="form_check()">Register</b-button>   
      </b-form>    
    </b-card> 
  </div>
</template>

<script> 
import axios from "axios";

export default {
  name: "Register",
  data(){
    return{
      register_form :{
        user:'',
        job:0,
        password:'',
        password2:''
      },
      job_options:[
        { value: 1, text: 'Koordinator' },
        { value: 2, text: 'Checklist' },
        { value: 3, text: 'Penimbang' }
      ]
    }
  },
  components: {
  },
  methods: {
    pass_check(pwd1,pwd2){
      return pwd1===pwd2 && pwd1 !== null && pwd2 !== null && pwd1 !== '' && pwd2 !== ''
    },
    form_check(){
      if (this.register_form.user != null && this.register_form.job != 0 && this.register_form.job != null 
      && this.pass_check(this.register_form.password, this.register_form.password2)) {
        return false
      }
      else {
        return true
      }
      
    },
    on_submit(event){
      // alert(`${this.register_form.user}, ${this.register_form.job}`)
      event.preventDefault();

      if (this.register_form.password === this.register_form.password2 && this.register_form.password.length > 0){
       axios
        .post(`/api/user/register`, {
          timeout: 5000,
          user: this.register_form.user,
          job: this.register_form.job,
          password: this.register_form.password
        })
        .then(() => {
          alert("Daftar Berhasil")
        })
        .catch((err) => {
          alert("Daftar Tidak Berhasil")
          console.log(err)
        });
      }
      else{
        alert('Daftar Tidak Berhasil')
      }
      this.register_form.user='',
      this.register_form.job=0,
      this.register_form.password='',
      this.register_form.password2=''
    }
  }
};
</script>
